@import url(https://fonts.googleapis.com/css?family=Fredoka+One|Open+Sans:300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Primary Font */
html, body{
  background-color: #282c34;
}


.App {
  /*text-align: center;*/
  width: 100vw;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  padding: 10px;
}

.arrows-container{
  position: absolute;
  bottom: 0px;
  width: 100vw;
  text-align:center;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.arrows-item{
  color: white;
  font-size: 3em;
}

.black-text{
  color: black !important;
}

.bg-1{
  background-image: url(/static/media/kobe_lay.dcfc1ae2.jpg);
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover;
}

.bg-2{
  background-image: url(/static/media/kobe_lay2.fd878204.jpg);
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover;
}

.bounce{
  -webkit-animation-name: bounce;
          animation-name: bounce;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
}

.display-none{
  display: none;
}

.footer-icon{
  font-size: 2em;
  color: #4CAF50;
}

.footer-section{
  position: relative;
  width: 29%;
  float: left;
  padding: 2%;
  /*background-color: #282c34;*/
  /*border: 1px solid red !important*/
}

.footer-text{
  font-size: 1em;
  font-family: 'Fredoka One', cursive;
  font-family: 'Open Sans', sans-serif;
  color: #FFF;
}

.footer-title{
  font-size: 1em;
  font-family: 'Fredoka One', cursive;
  color: #FFF;
  padding-bottom: 20px;
}

#footer-container{
  background-color: #282c34;
  position: relative;
  width: 100%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  /*overflow: auto;*/
}

#nav-title{
  padding: 20px;
  top: 12px;
  float: left;
}

.icons-container{
  position: relative;
  padding: 20px;
  bottom:10px;
  width: 50%;
  float: right;
  text-align: right;
}

.iconography{
  color: #4CAF50;
  font-size: 2em;
  /*background-color: #FFF;*/
  padding: 10px;
}

.sub-title{
  font-size: 3em;
  font-family: 'Fredoka One', cursive;
  color: #FFF;
}

.sub-text{
  font-size: 2em;
  margin-top: 20%;
  color: #FFF;
}

#navbar-container{
  position: relative;
  width: 100vw;
  height: 6vh;
 /*text-align: left;*/
}

.portfolio-1{
  position: relative;
  width: 100vw;
  height: 80vh;
  padding: 1%;
  background-color: #6F8E8B;
}

.text-left{
  text-align: left;
}

.text-right{
  text-align: right;
  padding-right: 20px;
}

.r-padding{
  padding-right: 50px;
}



@-webkit-keyframes bounce{
  0%   { transform: translateY(0); }
  50%  { transform: translateY(-35px); }
  100% { transform: translateY(0); }
}



@keyframes bounce{
  0%   { transform: translateY(0); }
  50%  { transform: translateY(-35px); }
  100% { transform: translateY(0); }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 2vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.arrows-item{
  width: 50%;
  padding: 50px;
}

.button-text{
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  /*margin: 10px;*/
  margin-top: 10px;
}

.button-text-focused{
  background-color: #FFF; /* Green */
  border: none;
  color: black;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 10px;
}


@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/*
Icon classes can be used entirely standalone. They are named after their original file names.

Example usage in HTML:

`display: block` sprite:
<div class="icon-home"></div>

To change `display` (e.g. `display: inline-block;`), we suggest using a common CSS class:

// CSS
.icon {
  display: inline-block;
}

// HTML
<i class="icon icon-home"></i>
*/
.icon-kobe_lay {
  background-image: url(/static/media/sprite.4b503bd9.jpg);
  background-position: 0px 0px;
  width: 5760px;
  height: 3840px;
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover;
}
.icon-kobe_lay2 {
  background-image: url(/static/media/sprite.4b503bd9.jpg);
  background-position: -5760px 0px;
  width: 5760px;
  height: 3840px;
}

.sprite-container{
  width: 100vw;
}

.sprite{
  /* Create a Responsive Square */
  width: 100%;
  height: 0;
  padding-bottom: 70%;

  /* Set The Sprite as the Background */
  background-image: url(/static/media/sprite_optimized_6.1f7928e4.jpg);
  background-position: 0 0;
  /*background-size: 200%; number of images horizontally * 100 */
  background-size: cover;
  display:block;
}

/* On Hover Show Second of Two Images */
.sprite2{
  background-position: 100% 0;
}
